.modal {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 99;
  background-color: rgb(0, 0, 0, 0.5)
}


/**
/* --- DETAILS section ---
/*
*/
.location-detail-page {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  background-color: var(--white-color);
  font-size: 0.875rem;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  margin: 60px auto 0 auto;
  border-radius: 5px;
  max-width: 800px;
  display: flex;
  flex-direction: column;
  -ms-flex-line-pack: justify;
      align-content: space-between;
}
.location-detail-page {
  padding: 1rem 0;
  outline: none;
}


.photos,
.facts,
.position {
  padding: 1rem 1.5rem;
}




/* NAME heading */
.location-name-header {
  margin: 0 1.5rem;
  color: var(--alt-dark-gray-color);
  text-align: left;
  padding-bottom: 0.5rem;
}

.alt-name {
  margin: 0;
  font-size: 0.875rem;
  font-weight: 400;

}
.name {
  margin: 0.5rem 0;
  font-size: 1.25rem;
  font-weight: 400;
}

.modal-body {
  
  overflow-y: auto;
  border-top: 1px solid var(--dark-gray-color);
  border-bottom: 1px solid var(--dark-gray-color);
}
@media screen and (max-width: 768px) {
  .modal-body {
    -ms-flex-positive: 1;
        flex-grow: 1;
  }
}
@media screen and (min-width: 769px) {
  .modal-body {
    max-height: calc(100vh - 17rem);
  }
}

/*
/*   Section Content
*/
.section-name {
  text-align: left;
  font-weight: 600;
  margin: 0.5rem 0;
  text-transform: uppercase;
}

.photos-gallery,
.photos-link,
.data-link,
.position-content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-line-pack: start;
      align-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
}


/* PHOTOS section */

.location-detail-page .photos {
  display: block;
  text-align: left;
}

.photos-gallery {
  padding-left: 0;
  margin: 1rem 0;
}


.photos-gallery li {
  margin: 0 0.5em 0.5em 0;
  width: 175px;
  height: 120px;
  background-color: var(--medium-gray-color);
}
.photos-gallery li:last-child {
  margin-right: 0;
}


.photos-gallery .photo {
  width: 100%;
  height: 100%;
  background-size: cover;
}

/* FACTS section */

.location-detail-page .facts {
  text-align: left;
  font-weight: 300;
  background-color: var(--theme-color);
}

.facts .content {
  font-size: 1rem;
  line-height: 1.5;
}
.facts .content span[lang="is"] {
  font-style: italic;
  font-weight: 400;
  color: var(--primary-color);
}
.facts .content i {
  font-weight: 400;
}

.data-link {
  margin: 0 0 1rem;
  font-weight: 400;
}

/* POSITION GPS section */

.location-detail-page .position {
  font-size: 0.875rem;
  font-weight: 400;
  color: var(--dark-gray-color);
}



.location-detail-page .coordinates {
  display: -ms-inline-flexbox;
  display: inline-flex;
}
.location-detail-page .coordinates p {
  text-align: left;
  margin: 0 1em 0 0;
}

.location-detail-page .coordinates span {
  font-family: 'Roboto Mono', monospace;
  color: var(--primary-color);
}


/* CLOSE button */

.location-detail-page .close {
  top: 0.5rem;
  right: 0.5rem;
}





@media screen and (max-width: 480px) {
  .photos-gallery li {
    width: 46%;
  }

}
@media screen and (max-width: 600px) {
  .photos-gallery li {
    height: 15vh;
  }

}
@media screen and (max-width: 768px) {
  .location-detail-page {
    height: 100vh;
    margin: 0;
  }
  .location-detail-page.error {
    max-width: 100%;
  }
  .photos-gallery {
    -ms-flex-pack: start;
        justify-content: flex-start;
  }
}

/* ERROR page */

.location-detail-page.error {
  position: relative;
  margin: 30% auto;
  max-width: 600px;
  height: auto;
}



