.locations-list .item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  font-family: 'Roboto Mono', monospace;
  border-bottom: 1px solid var(--dark-gray-color);
  text-decoration: none;
  -webkit-transition: max-height 150ms, background-color 300ms;
  -o-transition: max-height 150ms, background-color 300ms;
  transition: max-height 150ms, background-color 300ms;
}
.locations-list .item:focus {
  outline: none;
}


.locations-list .item.details-show {
  max-height: 100%;
  background-color: var(--white-color);
}

.locations-list .item.details-hidden {
  overflow: hidden;
  max-height: 4.75rem;
}

.item-name,
.details-container {
  padding: 1rem 0.5rem;
}

.item-link {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  text-decoration: none;
  color: var(--dark-gray-color);
  cursor: pointer;
}
.item-link:hover {
  color: var(--black-color);
  background-color: var(--light-gray-color);
}
.item-link.expanded:hover {
  background-color: var(--alt-light-gray-color);
}

.item-name h4 {
  font-family: 'Roboto', sans-serif;
  font-size: 0.75rem;
  font-weight: 500;
  margin: 0;
}
.item-name h2 {
  font-size: 1rem;
  font-weight: 300;
  margin: 0.5rem 0 0;
}
.item-link .chevron-icon {
  padding: 0.5rem;
}
.item-link .chevron-icon svg {
  width: 1.5rem;
  height: 1.5rem;
}


.details-container {
  font-family: 'Roboto', sans-serif;
  overflow: hidden;
}


.locations-list .item.details-show .details-container {
  -webkit-transition: translate(0, 0);
  -o-transition: translate(0, 0);
  transition: translate(0, 0);

}
.locations-list .item.details-hidden .details-container {
  -webkit-transition: translate(0, -300px);
  -o-transition: translate(0, -300px);
  transition: translate(0, -300px);
}





.locations-list .item ul {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  font-size: 0.75em;
}
.item-list-tags {
  margin-bottom: 1rem;
}

.item .tag {
  font-weight: 400;
  padding: 0.5em 0.75em;
  margin-right: 0.25rem;
  color: var(--alt-dark-gray-color);
  background-color: var(--medium-gray-color);
  border-radius: 8px;
}



.item-list-images {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  background-color: var(--medium-gray-color);
  -ms-flex-pack: center;
      justify-content: center;
  overflow-x: hidden;
  padding: 0.5rem;
}
.item-list-images.empty-state {
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  height: 8rem;
}
.item-list-images.empty-state p {
  font-weight: 500;
}

.item .photo {
  -ms-flex: 1 1;
      flex: 1 1;
  min-width: 6rem;
  height: 4rem;
  background-color: var(--alt-dark-gray-color);
  background-size: cover;
  margin: 0.125rem;
}

.item-list-bottom-panel {
  display: -ms-flexbox;
  display: flex;
  margin-top: 1rem;
  -ms-flex-pack: end;
      justify-content: flex-end;
}
.item-list-bottom-panel .button {
  margin-left: 0.5rem;
}


.item-list-empty-state {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  min-height: 10rem;
}


